
<template>
    <div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                        <button type="button" class="close" @click="closePopup">
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 name="header" class="modal-title py-3">
                            {{ FieldName }} {{ $t('Properties') }}
                        </h4>

                    </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en"  :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <!-- <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-2 text-danger float-left"
                                v-if="IsSystemGeneratedField == true">
                                <div class="alert alert-danger mb-0" role="alert">
                                    {{ $t('SystemGeneratedFieldMessage') }}
                                </div>
                                </div>   -->
                    </dynamic-form>
                </div>

                
                </div>
            </div>
        </div>
    </div>
</template>
<script>

import { DynamicForm } from 'tg-controls_cli/dist/myLib.common'
export default {
    components: {
        DynamicForm
    },
    props: {
        DataTypeId: {
            type: Number,
            required: true
        },
        FieldName: {
            type: String,
            required: true
        },
        LanguageArray:
        {
            type: Array,
            required: true
        },
        oldFieldPropertyData:
        {
            type: Array,
            required: true
        },
        AddCustomFields:
        {
            type: Boolean,
            required: true
        },
        bindEditedDataProperty:
        {
            type: Array,
            required: true
        },
        IsSystemGeneratedField:
        {
            type: Boolean,
            required: true
        }
    },
    data() {
        return {
            lenghtOfLanguageDiv: null,
            Lblname: '',
            myObj: [],
            IsRequired: null,
            displayName: '',
            ddlLanguage: '',
            LengthVal: '',
            DecimalVal: '',
            txtDescription: '',
            dummyArray: [],
            SelectedData: [],
            langSelectedData: [],
            displayNameTextData: [],
            diplayNameWithCulture: [],
            langCulture:[],
            languageCultureArray:[],
            displayNameData: '',
            IsSystemGeneratedField:false,
            FormSchema: [
                {
                    layoutType: "four",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Questions',
                            name: "Question",
                            value: this.FieldName,
                            placeholder: "",
                            disabled: false,
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: 'Pick List Option',
                            name: "PickListOption",
                            value: '',
                            visibility: false,
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "NumericField",
                            label: 'Length',
                            name: "Length",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            validationRules: { "required": true },

                        },
                        {
                            astype: "NumericField",
                            label: 'Decimal Places',
                            name: "DecimalPlaces",
                            value: '',
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": true },

                        },
                        {
                            astype: "CheckBoxField",
                            label: "Mark as Required",
                            name: "MarkasRequired",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{ value: "1", name: "" }]
                            }
                        },
                        {
                            astype: "TextAreaField",
                            label: 'Description',
                            name: "Description",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            config: {
                                rows: 3
                            }
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: 'Language',
                            name: "Language",
                            value: '',
                            placeholder: "",
                            chkIndex: 0,
                            visibility: false,
                            config: {
                                options: [],

                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextField",
                            label: 'Display Name',
                            name: "DisplayName",
                            value: '',
                            placeholder: "",
                            chktxtIndex: 0,
                            visibility: false,
                            validationRules: { "required": true, max: 250 },
                            config: {
                                showAddIcon: true,
                                onAddButtonClick: this.AddMultiLanguage
                            },
                        },

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup
                }
            ]
        }

    },                          
    created: function () {
        var vm = this;
        vm.ShowHideFields();
        vm.BindLangDDL();
        if (vm.oldFieldPropertyData.length != 0 || vm.AddCustomFields == "FromManage") {
            vm.EditBindFornData();
        }

    },
    methods: {
        // Bind edited form data into fields.
        EditBindFornData() {
            var vm = this;
            // if (vm.oldFieldPropertyData.length > 0) {

            if (vm.AddCustomFields == "FromManage") {
                var vv = vm.EditProperty;
                (vm.FormSchema)[0].Data[0].value = vm.EditProperty.display_name;
                (vm.FormSchema)[0].Data[1].value = vm.EditProperty.picklist_options != "undefind" ? vm.EditProperty.picklist_options : '';
                (vm.FormSchema)[0].Data[2].value = vm.EditProperty.length;
                (vm.FormSchema)[0].Data[3].value = vm.EditProperty.decimal_places != "undefind" ? vm.EditProperty.decimal_places : '';
                (vm.FormSchema)[0].Data[4].value = vm.EditProperty.is_required == true ? "1" : '';
                (vm.FormSchema)[0].Data[5].value = vm.oldFieldPropertyData.Description;
                
                vm.EditProperty.cultureDetails = vm.MakeNormalArray(vm.EditProperty.cultureDetails);
                var objCulture1 = {};
                vm.EditProperty.cultureDetails.forEach((itm) => {
                    objCulture1={};
                    objCulture1.value = itm.lang_id;
                    vm.languageCultureArray.push(objCulture1);
                    objCulture1={};
                    objCulture1.value = itm.localization_value;
                    vm.languageCultureArray.push(objCulture1);
                })
                if (vm.EditProperty.cultureDetails.length != 0) {
                    var lengthIndex = vm.languageCultureArray.length / 2;
                    var count = 1;
                    
                    vm.languageCultureArray.forEach((item, index) => {
                        if (count < lengthIndex) {
                            (vm.FormSchema)[1].Data.push({
                                astype: "SelectField",
                                label: 'Language',
                                name: "Language" + (vm.FormSchema)[1].Data.length,
                                value: "",
                                placeholder: "",
                                chkIndex: (vm.FormSchema)[1].Data.length,
                                config: {
                                    options: [],
                                },
                                validationRules: "required",
                            },
                                {
                                    astype: "TextField",
                                    label: 'Display Name',
                                    name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                                    value: "",
                                    placeholder: "",
                                    chktxtIndex: (vm.FormSchema)[1].Data.length,
                                    validationRules: { "required": true, max: 250 },
                                    config: {
                                        showAddIcon: true,
                                        onaddIconBackground: 'bg-danger',
                                        onAddButtonClick: vm.RemoveFile,
                                        addIcon: 'fa fa-trash'
                                    },
                                })
                            // vm.FormSchema[1].Data.forEach((item) => {
                            //     vm.Lblname = item.name
                            // })
                            vm.BindLangDDL(vm.Lblname);
                            count++;
                        }

                    })
                    vm.languageCultureArray.forEach((itm, indx) => {
                    (vm.FormSchema)[1].Data[indx].value = itm.value;
                })

                }
            }


            else {
                (vm.FormSchema)[0].Data[0].value = vm.oldFieldPropertyData[0].Name;
                (vm.FormSchema)[0].Data[1].value = vm.oldFieldPropertyData[0].PickListOption != "undefind" ? vm.oldFieldPropertyData[0].PickListOption : '';
                (vm.FormSchema)[0].Data[2].value = vm.oldFieldPropertyData[0].Length;
                (vm.FormSchema)[0].Data[3].value = vm.oldFieldPropertyData[0].DecimalPlaces != "undefind" ? vm.oldFieldPropertyData[0].DecimalPlaces : '';
                // (vm.FormSchema)[0].Data[4].value = vm.oldFieldPropertyData[0].IsRequired;
                if(vm.oldFieldPropertyData[0].IsRequired !=0)
                {
                    (vm.FormSchema)[0].Data[4].value="1";
                }
                else
                {
                    (vm.FormSchema)[0].Data[4].value='';
                }
                (vm.FormSchema)[0].Data[5].value = vm.oldFieldPropertyData[0].Description;
                if (vm.oldFieldPropertyData.length != 0) {
                    var lengthIndex = vm.oldFieldPropertyData[0].LanguageDivLength.length / 2;
                    var count = 1
                    vm.oldFieldPropertyData[0].LanguageDivLength.forEach((item, index) => {
                        if (count < lengthIndex) {
                            (vm.FormSchema)[1].Data.push({
                                astype: "SelectField",
                                label: 'Language',
                                name: "Language" + (vm.FormSchema)[1].Data.length,
                                value: "",
                                placeholder: "",
                                chkIndex: (vm.FormSchema)[1].Data.length,
                                config: {
                                    options: [],
                                },
                                validationRules: "required",
                            },
                                {
                                    astype: "TextField",
                                    label: 'Display Name',
                                    name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                                    value: "",
                                    placeholder: "",
                                    chktxtIndex: (vm.FormSchema)[1].Data.length,
                                    validationRules: { "required": true, max: 250 },
                                    config: {
                                        showAddIcon: true,
                                        onaddIconBackground: 'bg-danger',
                                        onAddButtonClick: vm.RemoveFile,
                                        addIcon: 'fa fa-trash'
                                    },
                                })
                            vm.FormSchema[1].Data.forEach((item) => {
                                vm.Lblname = item.name
                            })
                            vm.BindLangDDL(vm.Lblname);
                            count++;
                        }

                    })

                }
                vm.oldFieldPropertyData[0].LanguageDivLength.forEach((itm, indx) => {
                    (vm.FormSchema)[1].Data[indx].value = itm.value;
                })
                // }

            }
        },
        MakeNormalArray(value){
            try{
                return JSON.parse(value)
            }
            catch(ex)
            {
                return value
            }

        },
        // show hide controls on their data types
        ShowHideFields() {
            var vm = this;
            if (vm.IsSystemGeneratedField == true) {
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[0].disabled = true;
                (vm.FormSchema)[0].Data[2].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[5].visibility = false;
                (vm.FormSchema)[1].Data[0].visibility = false;
                (vm.FormSchema)[1].Data[1].visibility = false;
            }
            else {
                (vm.FormSchema)[1].Data[0].visibility = true;
                (vm.FormSchema)[1].Data[1].visibility = true;
                if (vm.FieldName == 'Decimal') {
                    (vm.FormSchema)[0].Data[3].visibility = true;
                }
                if (vm.FieldName == 'Select List' || vm.FieldName == 'Checkbox') {
                    (vm.FormSchema)[0].Data[1].visibility = true;
                }

                if (vm.FieldName == 'Date') {
                    (vm.FormSchema)[0].Data[1].visibility = false;
                    (vm.FormSchema)[0].Data[3].visibility = false;
                }
            }
        },
        // Bind language dropdwon 
        BindLangDDL(name) {   
            var vm = this;
            if (name == undefined) {
                vm.LanguageArray.forEach((itm, index) => {
                    (vm.FormSchema)[1].Data[0].config.options.push({
                        value: itm.lang_id,
                        name: itm.lang_name
                    })
                })
            }
            else {
                vm.LanguageArray.forEach((itm, index) => {
                    if (typeof (name) != 'undefined' && name != null) {
                        (vm.FormSchema)[1].Data.forEach((item, indx) => {
                            if (indx % 2 == 0) {
                                (vm.FormSchema)[1].Data[indx].config.options.push({
                                    value: itm.lang_id,
                                    name: itm.lang_name
                                })
                            }
                        })

                    }

                })
            }

        },
        closePopup() {
            var vm = this;
            //vm.$parent.closepopupfordraggedfield();
            if (vm.AddCustomFields == "FromManage") {
                vm.$emit('closepopup');
            }
            else {
                vm.$parent.closePropPopup();
            }
        },

        // Add lang and display name fields on click of plus button
        AddMultiLanguage() {
            var vm = this;
            (vm.FormSchema)[1].Data.push({
                astype: "SelectField",
                label: 'Language',
                name: "Language" + (vm.FormSchema)[1].Data.length,
                value: "",
                placeholder: "",
                chkIndex: (vm.FormSchema)[1].Data.length,
                config: {
                    options: [],
                },
                validationRules: "required",
            },
                {
                    astype: "TextField",
                    label: 'Display Name',
                    name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                    value: "",
                    placeholder: "",
                    chktxtIndex: (vm.FormSchema)[1].Data.length,
                    validationRules: { "required": true, max: 250 },
                    config: {
                        showAddIcon: true,
                        onaddIconBackground: 'bg-danger',
                        onAddButtonClick: vm.RemoveFile,
                        addIcon: 'fa fa-trash'
                    },
                })
            vm.FormSchema[1].Data.forEach((item) => {
                vm.Lblname = item.name
            })
            vm.BindLangDDL(vm.Lblname);
        },
        // Remove Element on click of delete button
        RemoveFile: function (e, field) {

            var vm = this;
            var checkindex = (vm.FormSchema)[1].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex - 1;
            if (checkindex > -1) {
                (vm.FormSchema)[1].Data.splice(checkindex, 1);

            }
            if (chechIndex1 > -1) {
                (vm.FormSchema)[1].Data.splice(chechIndex1, 1);
            }

        },
        //Submit data in an array to maintain the property on click of save button 
        onSubmit(formfields,e) {
             var vm = this;
            // var localization_id= 0;
            // var custom_field_id= 0;
            // var langId=0;
            // var langName='';
            // var user_guide='';
           // var propValues = e.controlledValues;
           
            vm.lenghtOfLanguageDiv = (vm.FormSchema)[1].Data;
            var objCulture={};
            vm.lenghtOfLanguageDiv.forEach((itm,indx)=>{
                if(itm.astype=="SelectField")
                {
                    objCulture.localization_id= 0;
                    objCulture.custom_field_id= 0;
                    objCulture.user_guide= '',
                    objCulture.lang_id=itm.value;
                }
                if(itm.astype=="TextField")
                {
                    objCulture.localization_value=itm.value;
                    vm.langCulture.push(objCulture)
                    objCulture={};
                }
            

            })
            var SelectedData = ""
            if ((vm.FormSchema)[1].Data.length > 0) {
                (vm.FormSchema)[1].Data.forEach((item, indx) => {
                    if (indx % 2 != 0) {
                        SelectedData += item.value + '^'
                    }
                    else {
                        SelectedData += item.value + ','
                    }
                })
                var AllData = SelectedData.length - 1
                vm.displayNameData = SelectedData.slice(0, AllData);
            }
            if (vm.EditProperty != undefined) {
                vm.EditProperty.cultureDetails = vm.langCulture;
                if((vm.FormSchema)[0].Data[4].value.length !=0)
                {
                    vm.EditProperty.is_required=(vm.FormSchema)[0].Data[4].value[0] != undefined ? true : false;
                }
                else
                {
                    vm.EditProperty.is_required='';
                }
                // vm.EditProperty.is_required = propValues.MarkasRequired;
                // vm.EditProperty.is_email_template=   $("#chkis_email_template").prop("checked") == true ? 1: 0;
                vm.EditProperty.display_name = propValues.Question
                // vm.EditProperty.name = propValues.Question;
                if (propValues.PickListOption != undefined) {
                    vm.EditProperty.picklist_options = propValues.PickListOption;
                }
                if (propValues.Length != undefined) {
                    vm.EditProperty.length = propValues.Length
                }
                // this.$options.parent.LayoutList[this.parentIndex].FieldData[this.childIndex] = item;

                if (vm.EditProperty.group_id > 0) {
                    vm.EditProperty.is_updated = 1;
                }
                 vm.$parent.closePropPopup();
            }
            else {
                let obj = {};
                obj = {
                    id: vm.DataTypeId,
                    Name: formfields.Question,
                    IsRequired: (vm.FormSchema)[0].Data[4].value.length !=0 ? (vm.FormSchema)[0].Data[4].value[1] : '',
                    LanguageName: vm.displayNameData,
                    displayName: formfields.Question,
                    Length: formfields.Length,
                    DecimalPlaces: formfields.DecimalPlaces,
                    Description: formfields.Description,
                    PickListOption: formfields.PickListOption,
                    LanguageDivLength: vm.lenghtOfLanguageDiv
                }
                vm.myObj.push(obj);
                if (vm.myObj.length != null) {
                    vm.$parent.closePropPopup(vm.myObj);
                }
            }
            vm.$parent.closepopupfordraggedfield();
        },
    }

}
</script>